* {
    box-sizing: border-box;
}

body {
    font-family: 'Poppins', sans-serif;
    margin: 0;
}

section {
    padding: 20px;
}

.bold {
    font-weight: 700;
}

.gray {
   color: gray;
}

nav {
    height: 70px;
    display: flex;
    padding: 20px 20px;
    box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
}

.nav-logo {
    max-width: 100px;
}

.hero-container {
    display: flex;
    flex-direction: column;
    padding: 20px;
}

.hero-container > h1 {
    /* font-size: 26px; */
    font-size: 36px;
    line-height: 1.1;
    font-weight: 600;
    margin-bottom: 10px;
}

.hero-container > p {
    max-width: 350px;
    margin: 5px 0;
}

.hero-container > img {
    max-width: 100%;
    max-height: 100%;
    align-self: center;
    margin-top: 10px;
    margin-bottom: 10px;
}

.cards-list {
    display: flex;
    flex-wrap: nowrap;
    gap: 20px; 
    overflow-x: auto;
}

.card {
    width: 175px;
    font-size: 12px;
    flex: 0 0 auto; 
    display: flex;
    flex-direction: column;
    position: relative; 
}

.card--image {
    width: 100%;
    border-radius: 9px;
    margin-bottom: 9px;
}

.card--title {
    overflow: hidden;
    text-overflow: ellipsis;
}

.card--stats {
    display: flex;
    align-items: center;
    margin-top: 5px;
    column-gap: 3px;
}

.card--star {
    height: 14px;
}

.card--price {
    margin-top: auto;
    margin-right: 3px;
}

#review-count {
    margin-right: 3px;
}

.card--badge {
    position: absolute; 
    top: 6px;
    left: 6px; 
    background-color: white;
    padding: 5px 7px; 
    border-radius: 2px;
    font-weight: bold;
}

/* Tablet Query  */
@media screen and (min-width: 500px) {
}

/* Desktop Query */
@media screen and (min-width: 1025px) {

  nav {
    padding: 20px 150px;
  }

  .hero-container > img {
    margin-top: 29px;
    margin-bottom: 20px;
}
  
  .hero-container > h1, .hero-container > p, section {
    margin-left: 130px;
  }

  .hero-container > h1 {
    font-size: 52px;
  }

  .hero-container h2 {
    font-size: 22px;
  } 

  .card {
    width: 300px;
    font-size: 14px;
  }
}